@import url('https://fonts.googleapis.com/css2?family=Catamaran:wght@100;200;300;400;500;600;700;800;900&family=Pompiere&display=swap');

$colorLight: #cccccc;
$colorDark: #333333;
$colorHighlight:#99cc99;
$colorHighlight2:#663366;
$tile: sevenOverlappingCircles, rhombitrihexagonal, tilings;
$key: random(3);
$bgtile: nth($tile, $key); 

body{
    background-color: #cccccc;
    font-family: 'Catamaran', sans-serif;
    min-height: 100vh;
    font-size: 20px;
    header{
        background-color: $colorDark;
        nav{
            .nav-link:visited, .navbar-brand, .navbar-brand:visited, .navbar-toggler-icon, .nav-link, .nav-link.disabled{
                color: $colorLight;
                font-family: 'Pompiere', sans-serif;
            }
            .nav-link:focus, .nav-link:active, .nav-link:hover, .navbar-brand:focus, .navbar-brand:hover{
                color: $colorHighlight;
            }
            .navbar-brand{
                font-size: 250%;
            }
            .nav-link{
                font-size: 140%;
            }
      }
    }
    main{
        section{
            margin-bottom: 3rem;
        }
        .lead{
            font-size: 130%;
        }
        h2{
            font-family: 'Pompiere', sans-serif;
            font-size: 180%;
            color: $colorHighlight2;
            margin-bottom: 2rem;
        }
        h1{
            color: $colorHighlight2;
            font-family: 'Pompiere', sans-serif;
        }
        h3{
            font-weight: bold;
            a{
                text-decoration: none;
            }
        }
        ion-icon{
            color: $colorHighlight2;
            font-size: larger;
        }
        a{
            color: $colorHighlight2;
        }
        .project-nav{
            background-image: url('/assets/media/#{$bgtile}.svg');
            background-color: $colorHighlight2;
            background-repeat: repeat;
            display: flex;
            align-items: center;
            .nav-link{
                color: $colorLight;
                font-family: 'Pompiere', sans-serif;
                font-size: 150%;
            }
            .nav-link:hover{
                color: $colorHighlight;
            }
        }
        .project-tab{
            min-height: 160px;
            border-radius: 6px;
            background-image: url('/assets/media/#{$bgtile}.svg');
            background-color: $colorHighlight2;
            background-repeat: repeat;
            display: flex;
            align-items: center;
            a{
                text-decoration: none;
                color: $colorLight;
                font-family: 'Pompiere', sans-serif;
                font-size: 180%;
                font-weight: 600;
                padding: 10px;
                text-transform: uppercase;
            }
        }
        .fieldnote{
            h1{
                color: $colorHighlight2;
                font-family: 'Pompiere', sans-serif;
            }
        }
        .webDev{
            a{
                text-decoration: none;
            }
            p{
                margin-bottom: 4px;
            }
        }
        .nav-tabs{
            border-color: $colorHighlight;
            .nav-link{
                color: $colorHighlight2;
                border-color: $colorHighlight;
            }
            .nav-link:hover{
                border-color: $colorHighlight2;
            }
            .nav-link.active{
                background-color: $colorHighlight2;
                color: $colorLight;
            }
        }
    }
    footer{
        font-size: 85%;
        ul{
            list-style: none;
            a{
                color: $colorDark;
            }
            a:hover{
                color: $colorHighlight2;
            }
        }
    }
}